export const submitValidation = ({ moduleEntry, moduleName, setConfirmation, dispatch, setIsRequired, setInvalidThreshold }) => {
    let isClear = []
    let js = { ...moduleEntry }
    let invalidThreshold = []
    const jsR = moduleEntry.jobPost.list.map(data => {
        let description = data.originalUom
        data.poList.map(po => {
            const qtyCount = totalListItemWeightCount({moduleList:po.itemList})
            const { packSize, uom } = po
            po.itemList.map(item => {
                const { qty, grade, pack, note } = item

                // if (po.uom === 'kg') {
                if ((qty == 0 || isNaN(qty)) && grade?.value === null) {
                    item.qtyIsEmpty = false
                    item.gradeIsEmpty = false
                    item.noteIsEmpty = false
                    item.packIsEmpty = false
                    item.packThresholdIsEmpty = false
                }
                else {
                    if (item?.grade?.label === 'Waste' || item?.grade?.label === 'Semi Waste') {
                        isNaN(qty) || qty == 0 || qty === "" || qty === null ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
                        !grade?.value ? item.gradeIsEmpty = true : item.gradeIsEmpty = false
                        grade?.label === 'Waste' && !note ? item.noteIsEmpty = true : item.noteIsEmpty = false
                        // isNaN(pack) || pack == 0 || pack === "" || pack === null ? item.packIsEmpty = true : item.packIsEmpty = false
                    }
                    else {
                        let grd = data.gradeList.filter(grd => grd.value === grade.value)
                        let ratio = grd[0].multiplier
                        // description = description.split(' ')
                        // description = description[description.length-1]
                        let standardWeight = description?.toLowerCase() == 'gram' ? packSize / 1000 : packSize
                        let inputWeight = qty / pack
                        inputWeight = inputWeight - (standardWeight / ratio)
                        inputWeight = inputWeight / (standardWeight / ratio)
                        inputWeight = inputWeight * 100
                        if (inputWeight >= 51 || inputWeight <= -20) {
                            item.packThresholdIsEmpty = true
                            invalidThreshold.push(true)
                        }
                        else {
                            item.packThresholdIsEmpty = false
                            invalidThreshold.push(false)
                        }
                        item.packThreshold = isFloat(inputWeight)
                        isNaN(qty) || qty == 0 || qty === "" || qty === null ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
                        isNaN(pack) || pack == 0 || pack === "" || pack === null ? item.packIsEmpty = true : item.packIsEmpty = false
                        !grade?.value ? item.gradeIsEmpty = true : item.gradeIsEmpty = false
                        grade?.label === 'Waste' && !note ? item.noteIsEmpty = true : item.noteIsEmpty = false
                    }
                }
                
                // 30-12-2020
                // qty to compare is required
                // const unitOfMeasuress = grade?.uom?.toLowerCase() === 'gram' ? 'kg' : grade?.uom ? grade?.uom : po.uom
                // const qtyToCompareCheck = po.uom !== 'kg' ? false : unitOfMeasuress === 'kg' ? false : true
                // if(qtyToCompareCheck){
                //     item.qtyToCompare || item.qtyToCompare === 0 ? item.qtyToCompareIsEmpty = false : item.qtyToCompareIsEmpty = true
                // }

                if(!item.qtyIsEmpty){
                    if(qtyCount > po.initialWeight){
                        console.log(qtyCount, po.initialWeight);
                        item.qtyIsEmpty = true
                    }
                    else item.qtyIsEmpty = false
                }

                // if(!item.qtyIsEmpty) po.initialWeightOver > po.initialWeight ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
                if (item?.qtyIsEmpty || item?.gradeIsEmpty || item.packIsEmpty || item?.noteIsEmpty || item?.qtyToCompareIsEmpty) isClear.push(false)
                else isClear.push(true)

                return item
            })
            return po
        })
        return data
    })


    //validate there is value
    let valid = false
    jsR.map(data => {
        data.poList.map(po => {
            po.itemList.map(item => {
                let i = false
                if (item.qty) valid = true
            })
        })
    })

    js.jobPost.list = jsR
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: js })
    if (!isClear.includes(false) && valid) {
        setIsRequired(false)
    }
    else {
        setIsRequired(true)
    }

    if (!invalidThreshold.includes(true)) {
        setInvalidThreshold(false)
    }
    else {
        setInvalidThreshold(true)
    }

    if (!isClear.includes(false) && valid && !invalidThreshold.includes(true)) {
        setConfirmation({ show: true, name: 'submit' })
    }
    else {
        setConfirmation({ show: false, name: null })
    }
}